    getSelectList() {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        let orgID = commonHeader.orgId;
        let userId = commonHeader.userId;
        let params = {
            listShowsIdType: '6',
            listShowsIdModule: '06',
            createOrg: orgID,
            createdByUserLogin: userId
        }
        this.selectListSevice.selectUserListShow(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // console.log(this.getHeaderList);
                // this.moneyList = [];
                if (data.exploreConditionObject && data.exploreConditionObject != '') {
                    this.arrList = []
                    let ssd = JSON.parse(data.exploreConditionObject)
                    if (ssd && ssd.length < this.allList.length) {

                    } else {
                        this.allList = JSON.parse(data.exploreConditionObject);
                    }
                    this.allList.forEach(item => {
                        if (item['isCheck']) {
                            this.arrList.push(item);
                        }
                    });
                    console.log(this.allList)
                    // let selectArray = {};
                    // eco.forEach(element => {
                    //     let select = element["taskTypeKey"]
                    //     Object.assign(selectArray, { select: true })
                    //     // selectArray.push(element.taskTypeKey)
                    // });
                    // this.allList.forEach(item => {
                    //     if (selectArray[item.value]) {
                    //         item.isCheck = true;
                    //     } else {
                    //         item.isCheck = false;
                    //     }
                    // })
                } else {
                    this.arrList = []
                    this.allList.forEach(item => {
                        item.isCheck = true;
                    })
                    this.allList.forEach(item => {
                        if (item['isCheck']) {
                            this.arrList.push(item);
                        }
                    });
                    console.log(this.arrList)
                }
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }
    //
    saveSelectList() {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        let orgID = commonHeader.orgId;
        let userId = commonHeader.userId;
        let params = {
            listShowsIdType: '6',
            listShowsIdModule: '06',
            createOrg: orgID,
            createdByUserLogin: userId,
            exploreConditionObject: JSON.stringify(this.allList)
        }
        this.selectListSevice.addUserListShow(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.display = false;
                // this.attrListSearch();
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }
